/* BASKET */
.BasketPage {
    margin-top: 100px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    overflow-y: scroll;
}

.BasketPage > :last-child {
    margin-bottom: 3%;
}

.basket-empty {
    font-size: 35px;
    height: 100%;
    font-family: 'Times New Roman', Times, serif;
    display: flex;
    align-items: center;
}

.basket-items {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.basket-item {
    box-sizing: border-box;
    border: 2px solid black;
    margin-top: 50px;
    height: 100px;
    width: 80%;
    background: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.basket-item-info {
    height: 100%;
    width: 90%;
    background: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 50px;
}

.basket-item-info > * {
    width: 50%;
}

.basket-item-image-container {
    height: 80%;
    width: 35%;
    display: flex;
}

.basket-item-image {
    height: 100%;
    background-size: contain;
}

.basket-item-name {
    font-size: 25px;
    /* font-weight: 600; */
}

.basket-item-quantity {
    font-size: 20px;
    /* font-weight: 600; */
}

.basket-item-total {
    font-size: 20px;
    /* font-weight: 600; */
}

.basket-item-delete {
    background: black;
    padding: 15px;
    margin: 0 50px;
    transition: 0.2s ease-out;
    cursor: pointer;
}

.basket-item-delete:hover {
    background-color: crimson;
    transform: scale(1.1);
}

.basket-checkout {
    margin-top: 50px;
    height: 100px;
    width: 80%;
    background: white;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.basket-checkout-total {
    font-size: 25px;
    font-weight: 600;
    margin-right: 3%;
}

.basket-checkout-disable {
    opacity: 0;
    visibility: hidden;
}

.basket-checkout-link {
    text-decoration: none;
    width: 150px;
    text-align: center;
}

.basket-checkout-button {
    font-size: 20px;
    padding: 20px;
    background: black;
    font-weight: 600;
    color: white;
    transition: 0.2s ease-out;
}

.basket-checkout-button:hover {
    transform: scale(1.05);
}